h4,
h5,
h6 {
  margin: 0;
}
h2 {
  font-size: 24px;
  font-weight: bold;
  text-transform: uppercase;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
p {
  margin: 0;
}
a {
  text-decoration: none;
  color: black;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}
textarea {
  resize: none;
}
section {
  position: relative;
  padding: 45px 0;
}
input,
select,
button {
  outline: none;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
.clearfix::after {
  content: "";
  display: table;
  clear: both;
}
.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.main {
  flex: 1 0 auto;
}
.container {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
  position: relative;
}
.icon {
  display: inline-block;
}
.button {
  padding: 0 30px;
  font-size: 18px;
  line-height: 50px;
  color: #222222;
  background-color: #FBD341;
  border-radius: 10px;
  cursor: pointer;
  border: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: 1000;
}
.header-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
}
.header-content {
  display: flex;
  flex-direction: column;
}
.header-descr {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  padding: 10px 5px 15px;
  font-size: 14px;
  color: #d9dcd6;
  font-weight: bold;
}
.header-descr__addr {
  position: relative;
  padding-left: 25px;
}
.header-descr__addr:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(../img/sprite/sprite.png);
  background-position: -48px 0px;
  width: 13px;
  height: 18px;
}
.header-descr__tel {
  margin-left: 40px;
  position: relative;
  padding-left: 25px;
}
.header-descr__tel:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(../img/sprite/sprite.png);
  background-position: 0px -24px;
  width: 17px;
  height: 17px;
}
.header-descr__tel a {
  color: #d9dcd6;
  font-family: 'Fira Sans', sans-serif;
}
.header-descr__tel span {
  font-weight: normal;
}
.header-descr__social {
  margin-left: 60px;
}
.header.fixed {
  position: fixed;
  background: #004c84;
}
.social {
  display: flex;
}
.social-item:last-child {
  margin-left: 25px;
}
.social-item__link--facebook i {
  background-image: url(../img/sprite/sprite.png);
  background-position: 0px 0px;
  width: 22px;
  height: 22px;
}
.social-item__link--instagram i {
  background-image: url(../img/sprite/sprite.png);
  background-position: -24px 0px;
  width: 22px;
  height: 22px;
}
.nav-toggle {
  display: none;
  position: relative;
  width: 30px;
  height: 25px;
  cursor: pointer;
}
.nav-toggle__wrap {
  text-align: right;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.nav-toggle__line {
  position: absolute;
  right: 0;
  width: 100%;
  height: 3px;
  display: block;
  background: #d9dcd6;
  border-radius: 5px;
}
.header.active .nav-toggle__line {
  background-color: #000;
}
.nav-toggle__line--top {
  top: 3px;
  transition: .3s;
}
.header.active .nav-toggle__line--top {
  top: 10px;
  transition: .3s;
  transform: rotate(45deg);
}
.nav-toggle__line--middle {
  top: 11px;
  transition: .3s;
  opacity: 1;
}
.header.active .nav-toggle__line--middle {
  display: none;
}
.nav-toggle__line--bottom {
  bottom: 3px;
  transition: .6s;
}
.header.active .nav-toggle__line--bottom {
  top: 10px;
  transition: .3s;
  transform: rotate(-45deg);
  transition: .6s;
}
.main-nav {
  background-color: #FBD341;
  border-radius: 10px;
}
.header.active .main-nav {
  display: block;
}
.main-nav__list {
  display: flex;
  position: relative;
  overflow: hidden;
}
.main-nav__list:hover {
  overflow: visible;
}
.main-nav__item {
  padding: 15px 0;
  margin: 0 25px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.main-nav__item:hover {
  cursor: pointer;
}
.main-nav__item:hover .dropdown {
  opacity: .8;
}
.main-nav__item--arrow {
  margin-right: 15px;
}
.main-nav__link {
  font-size: 18px;
  font-weight: bold;
  color: #1c1c1c;
}
.main-nav__link--arrow {
  position: relative;
  padding-right: 25px;
}
.main-nav__link--arrow:after {
  content: '';
  position: absolute;
  right: 0;
  top: 5px;
  bottom: 0;
  margin: auto 0;
  background-image: url(../img/sprite/sprite.png);
  background-position: 0px -43px;
  width: 16px;
  height: 8px;
}
.dropdown {
  position: absolute;
  max-width: 230px;
  top: 100%;
  padding: 0 25px;
  opacity: 0.8;
  background-color: #3397e2;
  box-shadow: -1.2px 3.8px 14.7px 0.3px rgba(0, 0, 0, 0.34);
  transition: 0.2s;
  opacity: 0;
}
.dropdown__item {
  margin: 15px 0;
}
.dropdown__link {
  color: #fff;
  line-height: 1.2;
  transition: 0.2s;
}
.dropdown__link:hover {
  color: #FBD341;
}
.main-slider {
  position: relative;
}
.main-slider__content {
  padding: 60px 0 45px;
  position: absolute;
  bottom: 200px;
  left: 0;
  width: 100%;
}
.main-slider__content::after {
  content: '';
  width: 100%;
  max-width: 1330px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 76, 132, 0.3);
  background-image: linear-gradient(96deg, transparent 0%, transparent 100%);
}
.main-slider__content .container {
  width: 100%;
  max-width: 1150px;
  margin: 0 auto;
  z-index: 10;
}
.main-slider__text {
  font-size: 30px;
  color: #fff;
  font-weight: bold;
}
.main-slider__img img {
  width: 100%;
  min-height: 740px;
  object-fit: cover;
}
.main-slider__btn {
  margin-top: 40px;
}
.main-slider__dots {
  position: absolute;
  left: 0;
  bottom: 25px;
  width: 100%;
  height: 20px;
}
.main-slider__dots .slick-dots {
  text-align: left;
  left: 0;
}
.main-slider__dots .slick-dots li {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.main-slider__dot {
  width: 14px;
  height: 14px;
  opacity: 0.7;
  background-color: white;
  border-radius: 50%;
  display: block;
  cursor: pointer;
}
.slick-active .main-slider__dot {
  background-color: #FBD341;
}
.price-content {
  padding: 0 20px;
}
.price-content__item {
  height: 0;
  overflow: hidden;
}
.price-content__item.active {
  height: 100%;
}
.price-tabs {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 70px;
}
.price-tab__item {
  display: inline-block;
  margin-right: 35px;
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.price-tab__item:last-child {
  margin-right: 0;
}
.price-tab__item.active {
  color: #FBD341;
}
.price-slider {
  width: 100%;
  max-width: 1780px;
  margin: 0 auto;
}
.price-slider__item {
  display: flex;
  width: 100%;
  max-width: 520px;
  padding: 15px;
  margin: 0 auto;
  background-color: #F8F7F7;
  border-radius: 7px;
}
.price-slider__img {
  width: 190px;
  height: 175px;
  margin-right: 30px;
}
.price-slider__img img {
  width: 100%;
  height: 100%;
  border-radius: 7px;
}
.price-slider__descr {
  width: 100%;
  max-width: 280px;
  padding-top: 10px;
  line-height: 1.4;
  color: #3a3a3a;
}
.price-slider__name {
  font-size: 17px;
  font-weight: bold;
  color: #000;
}
.price-slider__text {
  margin-top: 10px;
  padding-top: 15px;
  border-top: 1px solid #EAE9E9;
}
.price-slider__bottom {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}
.price-slider__cine {
  font-size: 20px;
  color: #000;
}
.price-slider__cine span {
  font-size: 11px;
  color: #898989;
  vertical-align: super;
}
.price-slider__btn {
  display: block;
  position: relative;
  width: 37px;
  height: 37px;
  background-color: #FBD341;
  border-radius: 50%;
}
.price-slider__btn i {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  background-image: url(../img/sprite/sprite.png);
  background-position: -63px -21px;
  width: 6px;
  height: 12px;
}
.price-slider .slick-prev {
  left: 0;
  width: 10px;
  z-index: 10;
}
.price-slider .slick-prev::before {
  content: '';
  background-image: url(../img/sprite/sprite.png);
  background-position: -48px -20px;
  width: 10px;
  height: 19px;
  display: block;
}
.price-slider .slick-next {
  right: 0;
  width: 10px;
  z-index: 10;
}
.price-slider .slick-next::before {
  content: '';
  background-image: url(../img/sprite/sprite.png);
  background-position: -63px 0px;
  width: 10px;
  height: 19px;
  display: block;
}
.our-advantages {
  background-color: #F8F7F7;
}
.our-advantages__title {
  margin-bottom: 60px;
}
.our-advantages__item {
  display: flex;
  align-items: center;
}
.our-advantages__img {
  max-width: 460px;
  margin-right: 90px;
}
.our-advantages__img img {
  width: 100%;
}
.our-advantages__text {
  width: 100%;
  position: relative;
  max-width: 590px;
  font-size: 16px;
  line-height: 24px;
  padding-right: 30px;
  border-right: 1px solid #EAE9E9;
}
.our-advantages__text::before {
  content: '';
  position: absolute;
  right: -4px;
  top: 0;
  width: 8px;
  height: 8px;
  background-color: #FBD341;
  border-radius: 50%;
}
.sentence {
  position: relative;
  background: url('../img/bg-form.jpg') center no-repeat;
  background-size: cover;
}
.sentence::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(6, 10, 86, 0.55);
}
.sentence__title {
  color: #ffffff;
  margin-bottom: 40px;
}
.main-form__wrap {
  background-color: rgba(0, 0, 0, 0.6);
  padding: 30px 30px;
}
.main-form input {
  padding: 5px;
  margin: 0 15px;
  background: none;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.6);
  color: #fff;
  font-size: 18px;
  line-height: 0;
}
.main-form input[type="text"] {
  width: 140px;
}
.main-form input[type="email"] {
  width: 190px;
}
.main-form input[type="tel"] {
  width: 150px;
}
.main-form input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}
.main-form .selectize-control {
  display: inline-block;
  vertical-align: middle;
  margin: 0 40px 0 15px;
  line-height: 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.6);
}
.main-form .selectize-control.single .selectize-input.input-active,
.main-form .selectize-input {
  background: none;
}
.main-form .selectize-input {
  width: 180px;
  padding: 4px 5px;
  border: none;
  text-align: left;
  border-radius: 0;
}
.main-form .selectize-input.items {
  color: #ffffff;
}
.main-form .selectize-dropdown {
  padding: 10px 5px;
}
.main-form .selectize-dropdown,
.main-form .selectize-input,
.main-form .selectize-input input {
  font-size: 18px;
}
.main-form .selectize-dropdown-content .option.active,
.main-form .selectize-dropdown-content .option:hover {
  color: #FBD341;
  cursor: default;
  background-color: #fff;
}
.main-form .selectize-control.single .selectize-input:after {
  border: none;
  background-image: url(../img/sprite/sprite.png);
  background-position: -19px -24px;
  width: 16px;
  height: 8px;
}
.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  color: #d9dcd6;
}
.footer__wrap {
  padding: 50px 0 30px;
  background-color: #22334D;
  flex: 0 0 auto;
}
.footer-title {
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 40px;
}
.footer-info {
  width: 320px;
}
.footer-info__logo {
  display: block;
  margin-bottom: 30px;
}
.footer-info__text {
  padding: 25px 0;
  margin-bottom: 20px;
  line-height: 24px;
  border-top: 1px solid rgba(255, 255, 255, 0.6);
  border-bottom: 1px solid rgba(255, 255, 255, 0.6);
}
.footer-info__dev {
  vertical-align: top;
}
.footer-info__dev a {
  vertical-align: text-top;
  display: inline-block;
}
.footer-nav__item {
  margin-bottom: 20px;
}
.footer-nav__item:last-child {
  margin-bottom: 0;
}
.footer-nav__item a {
  display: block;
  color: #d9dcd6;
}
.footer-support__item {
  margin-bottom: 20px;
}
.footer-support__item:last-child {
  margin-bottom: 0;
}
.footer-support__item a {
  display: block;
  color: #d9dcd6;
}
.footer-contacts__addres {
  position: relative;
  padding-left: 25px;
  margin-bottom: 20px;
}
.footer-contacts__addres::before {
  content: '';
  position: absolute;
  left: 0;
  top: -3px;
  background-image: url(../img/sprite/sprite.png);
  background-position: -48px 0px;
  width: 13px;
  height: 18px;
}
.footer-contacts__tel {
  position: relative;
  padding-left: 25px;
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.6);
}
.footer-contacts__tel::before {
  content: '';
  position: absolute;
  left: 0;
  top: -3px;
  background-image: url(../img/sprite/sprite.png);
  background-position: 0px -24px;
  width: 17px;
  height: 17px;
}
.footer-contacts__tel a {
  color: #d9dcd6;
}
.footer-contacts__social {
  margin-left: 5px;
}
@media only screen and (min-width: 1921px) {
  .main-slider__content::after {
    max-width: 67%;
  }
}
@media only screen and (max-width: 1166px) {
  .main-form {
    text-align: center;
  }
  .main-form .selectize-dropdown-content {
    text-align: left;
  }
  .main-form__btn {
    margin: 20px auto 0;
    display: block;
  }
  .main-form input {
    margin-bottom: 20px;
  }
}
@media only screen and (max-width: 992px) {
  .header-content {
    flex-direction: column-reverse;
  }
  .main-nav {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
  .main-nav__list {
    display: inline-flex;
    flex-direction: column;
  }
  .main-nav__item {
    position: relative;
  }
  .main-nav__item:hover .dropdown {
    position: static;
    float: right;
    margin-left: 20px;
    display: block;
    background: #FBD341;
    opacity: 1;
  }
  .main-nav__item:hover .dropdown__link {
    font-weight: bold;
    color: #000;
  }
  .main-nav__item:hover .dropdown__link:hover {
    color: #000;
  }
  .main-nav__item:hover .main-nav__link--arrow::after {
    top: 2px;
    transform: rotate(-90deg);
    transition: .3s;
  }
  .dropdown {
    display: none;
  }
  .nav-toggle {
    display: inline-block;
  }
  .our-advantages__item {
    display: block;
  }
  .our-advantages__img {
    float: left;
    margin-right: 50px;
  }
  .our-advantages__text {
    max-width: 100%;
  }
  .footer-info {
    width: 70%;
    margin-bottom: 20px;
  }
  .footer-nav {
    width: 33%;
    margin: 40px 0;
  }
  .footer-support {
    width: 33%;
    margin: 40px 0;
  }
}
@media only screen and (max-width: 814px) {
  .header-descr {
    display: none;
  }
  .header-descr__social {
    margin-top: 20px;
    width: 100%;
    justify-content: flex-end;
  }
  .our-advantages__img {
    max-width: 350px;
  }
}
@media only screen and (max-width: 768px) {
}
@media only screen and (max-width: 686px) {
  .header-descr__tel {
    margin-top: 20px;
  }
  .main-slider__content {
    bottom: 100px;
  }
  .main-slider__text {
    font-size: 20px;
  }
  .main-slider__img img {
    min-height: 600px;
  }
  .button {
    font-size: 14px;
  }
}
@media only screen and (max-width: 666px) {
  .main-form input[type="text"] {
    width: 180px;
  }
  .main-form input[type="email"] {
    width: 180px;
  }
  .main-form input[type="tel"] {
    width: 180px;
  }
  .main-form .selectize-control {
    margin: 0 15px;
  }
  .footer-info {
    width: 100%;
  }
}
@media only screen and (max-width: 600px) {
  h2 {
    font-size: 20px;
  }
  .price {
    padding-bottom: 20px;
  }
  .price-tabs {
    margin-bottom: 20px;
    font-size: 16px;
  }
  .price-tab__item {
    padding: 10px;
    margin-right: 15px;
    border: 1px solid #000;
    border-radius: 5px;
  }
  .price-tab__item.active {
    border-color: #FBD341;
  }
  .price-slider {
    margin-bottom: 20px;
    padding-bottom: 40px;
  }
  .price-slider__item {
    flex-wrap: wrap;
    max-width: 340px;
  }
  .price-slider__img {
    width: auto;
    height: auto;
    margin: 0 auto 20px;
  }
  .price-slider__descr {
    max-width: 100%;
  }
  .price-slider .slick-next,
  .price-slider .slick-prev {
    top: 100%;
  }
  .price-slider .slick-prev {
    left: 40%;
  }
  .price-slider .slick-next {
    right: 40%;
  }
  .our-advantages__title {
    margin-bottom: 30px;
  }
  .our-advantages__img {
    max-width: 100%;
    margin: 0 auto 20px;
    float: none;
  }
  .our-advantages__text {
    text-align: justify;
  }
}
@media only screen and (max-width: 480px) {
  .header-inner {
    flex-wrap: wrap;
  }
}
@media only screen and (max-width: 360px) {
}
